.pagination{display:flex;justify-content:center}.pagination__button{cursor:pointer;background-color:#555555;font-family:'Montserrat', Helvetica, Arial, sans-serif;font-size:20px;color:#fff;border:1px solid #ffffff;border-radius:3px;width:50px;height:50px;margin:0 2px}.pagination__button:hover{background-color:#424242}.pagination__button--active{background-color:#424242;font-family:'Montserrat', Helvetica, Arial, sans-serif;font-size:20px;color:#fff;font-weight:bold;width:50px}
