.search-filter{display:flex;font-family:'Montserrat', Helvetica, Arial, sans-serif;font-size:15px;color:#fff;height:35px}.search-filter__title{line-height:35px;color:#ffffff;margin-right:20px}.search-filter__filters .left{border-radius:4px 0 0 4px}.search-filter__filters .right{border-radius:0 4px 4px 0}.search-filter__filters .search-button{font-family:'Montserrat', Helvetica, Arial, sans-serif;font-size:15px;color:#fff;padding:0px 30px;height:35px;background-color:#424242;outline:none;border:none;opacity:0.8}.search-filter__filters .search-button--active{padding:0px 30px;height:35px;font-family:'Montserrat', Helvetica, Arial, sans-serif;font-size:15px}
